export class Util {
    static SPACE_CHARACTER = " ";
    static SPECIAL_CHARACTERS = "!@#$%^&*()_+-=[]{}|;':\",.<>/?`~";
    static removeNonAlphabeticCharacterFrom(word) {
        return word
            .split("")
            .filter(char => isNaN(Number(char)))
            .filter(char => char !== this.SPACE_CHARACTER)
            .filter(char => !this.SPECIAL_CHARACTERS.includes(char))
            .join("");
    }
}
